import React from 'react'
import { FormControl, InputLabel, MenuItem, Select } from '@mui/material';

export default function dropdown({ data, level, onDropDownSelect, value }) {
    // console.log(data, level, value)
    let displayData = data

    if (level === 'second') {
        displayData = data.map(d => d.type)
    } else {
        displayData = Object.keys(data).map(e => {
            let displayedVal
            if (e.toLowerCase().includes('weight')) {
                displayedVal = 'weight-basis'
            } else if (e.toLowerCase().includes('age')) {
                displayedVal = 'age-basis'
            } else {
                displayedVal = e
            }
            // console.log({ val: e, disp: displayedVal })
            return { val: e, disp: displayedVal }
        })
    }

    return (
        <FormControl fullWidth>
            <InputLabel>Options</InputLabel>
            <Select
                value={value}
                label="Options"
                onChange={(e) => onDropDownSelect(e, level)}
            >
                {displayData.map(o => {
                    console.log(o.val, o.disp, level, typeof o)
                    if (typeof o === 'object') {
                        return <MenuItem value={o.val} key={o.val}>{o.disp}</MenuItem>
                    } else {
                        return <MenuItem value={o} key={o}>{o}</MenuItem>
                    }
                }
                )}
            </Select>
        </FormControl>
    )
}